// default layout

$default-border-color : #e6e6e6
$select-border-color: #969696
$red: #f3546a
$add-to-cart: #464646

$default-column-padding: 20px
$tablet-padding: 12px
$container-tablet-padding: 34px

$tablet-breakpoint: 1023
$mobile-breakpoint: 767
$small-mobile-breakpoint: 480
$button-hover: #f3546a

.clearfix
	*zoom: 1

.clearfix:before,
.clearfix:after
	display: table
	content: ""
	line-height: 0

.clearfix:after
	clear: both

a,
a:before,
.button,
button,
button:before,
input[type="button"],
input[type="submit"]
	transition: border-color .1s ease-in, color .1s ease-in, background-color .1s ease-in

img
	text-decoration: none

button
	background-color: transparent
	cursor: pointer


.center-row
	margin: 0 auto
	@extend .clearfix
	position: relative

.container
	margin: 0 auto
	max-width: 1024px
	box-sizing: border-box
	padding: 0 20px


.row
	margin: 0 20px
	overflow: hidden
	text-align: center
	margin: 2% 0
	+max-screen(1200)
		margin: 1% 0



%col2-3,
%col1-3
	float: left
	box-sizing: border-box
	display: block
	padding: 0 $default-column-padding
	+screen($mobile-breakpoint, $tablet-breakpoint)
		padding: 0 $tablet-padding
	+max-screen($mobile-breakpoint)
		width: 100%

.page-slider
	position: relative
	z-index: 5
.page-section
	text-align: center
	box-sizing: border-box
.page__inner
	padding-left: 5%
	padding-right: 5%
