.logo-wrap
	position: absolute
	left: 0
	z-index: 6
	height: 81px
	text-align: center
	display: table
	table-layout: fixed
	width: 100%
	&__i
		display: table-cell
		vertical-align: middle
		width: 100%
		height: 100%
	a
		font-size: 18px
		font-weight: 800
		line-height: 1em
		letter-spacing: 0.2em
		text-transform: uppercase
		color: #161a1b
		text-decoration: none
		display: inline-block
		position: relative

		&:after
			content: ""
			display: inline-block
			display: block
			margin: 0 auto
			margin-top: 10px
			width: 60px
			height: 1px
			background: #00d9ec
			+min-screen(1200)
				margin-top: 20px